<template>
  <div class="page">
    <headerNav title="Report" />

    <ul>
      <van-list
        v-model="vantList.loading"
        :finished="vantList.finished"
        @load="onLoad"
      >
        <li
          v-for="(item, index) in vantList.report"
          :key="index"
          class="list-item"
        >
          <van-cell-group>
            <van-cell title="Subject" :value="item.subject_title" />

            <div
              class="van-cell-wrap van-hairline--top-bottom"
              v-if="item.info  "
            >
              <van-collapse v-model="item.infoCollapseModel">
                <van-collapse-item   :title="item.infoCollapseTitle" :name="item.infoCollapseName">
                <div
                    class="van-cell-item van-cell-item-height"
                    v-html="item.info"
                  ></div>
                 
                </van-collapse-item>
              </van-collapse>
            </div>

            <div
              class="van-cell-wrap van-hairline--top-bottom"
              v-if="item.wiki"
            >
              <van-collapse v-model="item.wikiCollapseModel">
                <van-collapse-item :title="item.wikiCollapseTitle" :name="item.wikiCollapseName">
                  <div
                    class="van-cell-item van-cell-item-height"
                    v-html="item.wiki"
                  ></div>
                </van-collapse-item>
              </van-collapse>
            </div>




            <div
              class="van-cell-wrap van-hairline--top-bottom"
              v-if="item.suggest"
            >
              <van-collapse v-model="item.suggestCollapseModel">
                <van-collapse-item :title="item.suggestCollapseTitle" :name="item.suggestCollapseName">
                  <div
                    class="van-cell-item van-cell-item-height"
                    v-html="item.suggest"
                  ></div>
                </van-collapse-item>
              </van-collapse>
            </div>


          </van-cell-group>
        </li>
      </van-list>
    </ul>
  </div>
</template>

<script>
import { getProduct, report } from "../api/page.js";

import headerNav from "../components/header/nav";

export default {
  name: "result",
  data() {
    return {
      route: {},
      vantList: {
        productid: "",
        loading: false,
        finished: false,
        report: [],
      },
    };
  },
  components: { headerNav },
  computed: {},
  created() {
    // this.onLoad()
  },
  mounted() {
    // console.log(this.$route)
    if (this.$route && this.$route.params) {
      this.route.params = this.$route.params;
      this.onLoad();
    }
  },
  methods: {
    onLoad() {
      let areaParams = {};

      let num = 0;
      this.route.params.area.map((item, index) => {
        if (index % 2 == 1) {
          num++;
          // let tempkey = ''+num
          // areaParams[num] = Number(this.route.params.area[index]['flag'])  + Number(this.route.params.area[index-1]['flag'])
          let cflag = Number(this.route.params.area[index - 1]["flag"]),
            tflag = Number(this.route.params.area[index]["flag"]);
          // console.log(cflag, tflag )
          if (cflag && tflag) {
            areaParams[num] = 3;
          } else if (cflag && !tflag) {
            areaParams[num] = 2;
          } else if (!cflag && tflag) {
            areaParams[num] = 1;
          } else if (!cflag && !tflag) {
            areaParams[num] = 0;
          }
        }
      });
      // console.log(areaParams);

      let formdata = new FormData();
      formdata.append("serial", this.route.params.serial);
      formdata.append("unit", JSON.stringify(areaParams));

      let params = {
        serial: this.route.params.serial,
        unit: areaParams,
      };

      report(formdata).then((response) => {
        //  console.log( response )
        if (response.length) {
          response.map((item, index) => {
            if (item.info) {
              item.infoCollapseTitle = "Result";
              item.infoCollapseName = "1" + index;
              item.infoCollapseModel = [];
              item.infoCollapseModel.push(item.infoCollapseName)
            }
            if (item.wiki) {
              item.wikiCollapseTitle = "Wiki";
              item.wikiCollapseName = "2" + index;
              item.wikiCollapseModel = [];
            }
            if (item.suggest) {
              item.suggestCollapseTitle = "Suggest";
              item.suggestCollapseName = "3" + index;
              item.suggestCollapseModel = [];
            }
          });
        }

        console.log(response)

        this.vantList.report = response;

        this.vantList.loading = false;
        this.vantList.finished = true;
      });
    },
  },
};
</script>

